<template>
<div>

  <div v-for="book in getSource()">

    {{book.Source}}

    id{{book.SSBookClistId}}

    {{book.WordNum}}
  </div>

</div>
</template>

<script>
export default {
  name: "ChangeSource",
  methods: {
    getSource() {
      var rList = []
      var SSBookClistId = JSON.parse(this.$store.state.noveldetail);
      var clist = SSBookClistId["Clist"]
      for (var i=0; i < clist.length; i++) {
        console.log(clist[i])
        rList.push(clist[i])
      }
      return rList
    }
  }
}
</script>

<style scoped>

</style>



