<template>
  <div>
    <!--   -->
    <div class="container sticky-top">
      <div class="row justify-content-center menu">
        <svg t="1590573499536" style="font-size: 1.5rem;" @click="goBack" class="icon" viewBox="0 0 1024 1024"
             version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3861" width="32" height="32">
          <path
            d="M608 736c-6.4 0-19.2 0-25.6-6.4l-192-192C384 524.8 384 499.2 390.4 486.4l192-192c12.8-12.8 32-12.8 44.8 0s12.8 32 0 44.8L460.8 512l166.4 166.4c12.8 12.8 12.8 32 0 44.8C627.2 736 614.4 736 608 736z"
            p-id="3862"></path>
        </svg>
        <div class="col-4"></div>
        <div class="col-4" @click="goChangeSource"></div>

      </div>
    </div>
    <!--   -->
    <div class="bklists">
<!--      <el-scrollbar>-->
        <ul class="list-group list-group-flush " data-spy="scroll" data-target="#bklists" data-offset="0"
            v-for="(Lists,index) in chapterlists" :key="index">
          <li class="list-group-item" @click="url2reader(Lists.Lurl,Lists.Lname,index)">{{ Lists.Lname }}
          </li>
        </ul>

    </div>

  </div>
</template>

<script>

import {Indicator} from "mint-ui";

export default {
  name: "ChapterList",
  methods: {
    goChangeSource(){
      this.$router.push({path: "/changesource"});
    }
    ,
    getchapterlists() {
      console.log('nd ')
      var request = window.indexedDB.open('bookdb', 1);
      request.onsuccess = (event) => {
        var db = event.target.result;
        var transaction = db.transaction(['chapterlist'], 'readwrite');
        var objectStore = transaction.objectStore('chapterlist');
        var index = objectStore.index('chapterurl');
        index.get(this.bookid).onsuccess = (event) => {
          if (event.target.result === undefined) {
            console.log("")
            var SSBookClistId = JSON.parse(this.$store.state.noveldetail)
            console.log(SSBookClistId)
            let user = {
              SSBookClistId: SSBookClistId["Clist"][0]["SSBookClistId"]
            };
            this.$api.chapter(user)
              .then((response) => {
                // response => url
                var response = response.data;
                if (response["status"] === 1) {
                  var chapterlist = response["data"];
                  this.$store.commit("setchapterlist", chapterlist);

                  var request = window.indexedDB.open('bookdb', 1);
                  request.onsuccess = (event) => {
                    var db = event.target.result;
                    var transaction = db.transaction(['chapterlist'], 'readwrite');
                    var objectStore = transaction.objectStore('chapterlist');
                    //   'allchapter'
                    objectStore.add({chapterurl: this.bookid, content: chapterlist});
                  };
                }

              })
              .catch(function (error) {
                console.log(error)
              });
          } else if (event.target.result !== undefined) {
            console.log("")
            var chapterlist = event.target.result["content"];
            this.$store.commit("setchapterlist", chapterlist);
          }
        };
      };
    },
    url2reader(chapterurl, chaptername, index) {
      this.$store.commit("setshowindex", 0);
      this.$store.commit("setchapterindex", index);
      this.$router.push({path: '/reader'});
    },
    goBack() {
      var lasturl = this.$store.state.lasturl
      if (lasturl ===undefined){
        this.$router.push({path: "/"});
      }
      this.$router.push({path: this.$store.state.lasturl});
    }
  },
  computed: {
    chapterlists() {
      return eval(
        "(function(){return " + this.$store.state.chapterlists + ";})()"
      );
    }
  },
  mounted() {
    this.$store.commit('updaterouter', "chapterlist");
  },
  beforeCreate() {
    Indicator.open('...');
  },
  created() {
    setTimeout(() => Indicator.close(), 0)
  },
  beforeUpdate() {
    Indicator.open('...');
  },
  updated() {
    setTimeout(() => Indicator.close(), 0)
  }
}
</script>

<style scoped>
ul {
  /* margin-top: 5px; */
  margin: 0px;
}

.menu {
  text-align: center;
  height: 2rem;
  line-height: 2rem;
}

.el-scrollbar__wrap {
  overflow-x: hidden;
}

.container {
  background-color: #f8f9fa;
  border-bottom: 1px solid;
  border-bottom-color: #c3c3c357;
}

span {
  color: black;
}

.col-11 {
  margin-top: 9px;
}

span i {
  font-weight: bold;
}
</style>



