<template>
  <div>
    <div :class="HistoryStore"></div>
  </div>
</template>

<script>
export default {
  methods: {
    gettime() {
      var nowtime = new Date().getTime();
      return nowtime;
    },
    updateHS(bookid, datas) {
      var HSS = localStorage.getItem("HS");
      if (HSS === null || undefined) {
        obj = {};
        obj[bookid] = datas;
        localStorage.setItem("HS", JSON.stringify(obj));
      } else {
        var obj = JSON.parse(HSS);
        obj[bookid] = datas;
        localStorage.setItem("HS", JSON.stringify(obj));
      }
    }
  },
  computed: {
    HistoryStore() {
      var HSbookid = this.$store.state.bookid;
      var HSchapterindex = this.$store.state.chapterindex;
      var HSshowindex = this.$store.state.showindex;
      var HSD = {
        chapterindex: HSchapterindex,
        showindex: HSshowindex,
        lasttime: this.gettime()
      };
      this.updateHS(HSbookid, HSD);
      return "HistoryStore"
    }
  },
};
</script>

<style>
</style>



