import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index/Index'
import SearchResults from '@/components/Search/SearchResults'
import NovelDetail from '@/components/Detail/NovelDetail'
import ChapterList from '@/components/Chapterlist/ChapterList'
import Reader from '@/components/Reader/Reader'
import Reader1  from '@/components/Reader/reader1'
import bookshelf from '@/components/Bookshelf/bookshelf'
import ChangeSource from "../components/ChangeSource/ChangeSource";


Vue.use(Router);

export default new Router({
  routes: [

    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/results',
      name: 'SearchResults',
      component: SearchResults
    },
    {
      path: '/detail',
      name: 'NovelDetail',
      component: NovelDetail
    },
    {
      path: '/chapterlist',
      name: 'ChapterList',
      component: ChapterList
    },
    {
      path: '/reader',
      name: 'Reader',
      component: Reader
    },
    {
      path: '/reader1',
      name: 'Reader1',
      component: Reader1
    },
    {
      path: '/bookshelf',
      name: 'bookshelf',
      component: bookshelf
    },
    {
      path: '/changesource',
      name: 'changesource',
      component: ChangeSource
    },
  ]
})




